.main_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}
.bmi_container{
    box-shadow: 0px 0px 12px #ccc;
    border-radius: 8px;
    padding: 1rem;
}
input{
    width: 100%;
    font-size: 1.2rem;
    padding: 8px 4px;
    margin: 8px 0;
    border-radius: 8px;
}

.cal_btn{
    display: block;
    width: 100%;
    font-size: 1.2rem;
    margin: 8px 0;
    padding: 10px 0;
    background-color: #333;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 8px;
    cursor: pointer;
}
.reload_btn{
    display: block;
    width: 100%;
    font-size: 1.2rem;
    margin: 8px 0;
    padding: 10px 0;
    background-color: #fff;
    color: #333;
    border: 1px solid #333;
    border-radius: 8px;
    cursor: pointer;
}
.text{
    text-align: center;
    margin: 24px;
}
p{
    margin: 10px 0;
}
.img_container{
    text-align: center;
}
.img_container img{
    height: 200px;
}
